.transaction-history {
  width: 900px;

  box-shadow: 1px 1px 9px rgba(0, 0, 0, 0.5);
  text-align: center;
}

.transaction-history th {
  height: 60px;

  background: rgb(0, 185, 209);
}
.transaction-history tr {
  height: 60px;
}

.transaction-history tr:nth-child(2n) {
  background: rgb(209, 209, 209);
}
